@import url('https://fonts.googleapis.com/css2?family=Varta:wght@515&display=swap');

* {
  background-color: rgb(255, 255, 255);
  box-sizing: border-box;

}

.item{
  color: grey;
  margin: 20px;
  font-weight: 30px;
  font-family: 'Varta', sans-serif;
  text-decoration: none;
}

.item_underlined{
  color: rgb(92, 16, 16);
  margin: 20px;
  font-weight: 30px;
  font-family: 'Varta', sans-serif;
  text-decoration: none;

}

.social{
  margin-left:auto;
  margin-right:20px;
}


.text-block {
  position: absolute;
  bottom: 30px;
  font-size: 15px;
  font-family: 'Varta', sans-serif;
  left: 50%;
  margin-left: -32px;
  color: #2c6277;
  }
  

.gallery-container{
  padding-top: 50px;
  text-align: center;
  line-height: 0px;
  padding-bottom: 50px;
}



.parallax {
  /* The image used */
  background-image: url("photos/take_flight/bluebird.jpg");

  /* Set a specific height */
  min-height: 500px;

  /* Create the parallax scrolling effect */
  background-attachment: fixed;
  background-position: left;
  background-repeat: no-repeat;
  background-size: cover;
}

.parallax1 {
  /* The image used */
  background-image: url("photos/take_flight/swans.jpg");

  /* Set a specific height */
  min-height: 500px;

  /* Create the parallax scrolling effect */
