@import url(https://fonts.googleapis.com/css2?family=Varta:wght@515&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  background-color: rgb(255, 255, 255);
  box-sizing: border-box;

}

.item{
  color: grey;
  margin: 20px;
  font-weight: 30px;
  font-family: 'Varta', sans-serif;
  text-decoration: none;
}

.item_underlined{
  color: rgb(92, 16, 16);
  margin: 20px;
  font-weight: 30px;
  font-family: 'Varta', sans-serif;
  text-decoration: none;

}

.social{
  margin-left:auto;
  margin-right:20px;
}


.text-block {
  position: absolute;
  bottom: 30px;
  font-size: 15px;
  font-family: 'Varta', sans-serif;
  left: 50%;
  margin-left: -32px;
  color: #2c6277;
  }
  

.gallery-container{
  padding-top: 50px;
  text-align: center;
  line-height: 0px;
  padding-bottom: 50px;
}



.parallax {
  /* The image used */
  background-image: url(/static/media/bluebird.68253b1a.jpg);

  /* Set a specific height */
  min-height: 500px;

  /* Create the parallax scrolling effect */
  background-attachment: fixed;
  background-position: left;
  background-repeat: no-repeat;
  background-size: cover;
}

.parallax1 {
  /* The image used */
  background-image: url(/static/media/swans.59073d90.jpg);

